import { createBrowserClient } from "@supabase/ssr"

let supabaseClient: ReturnType<typeof createBrowserClient> | null = null

export function getSupabaseClient() {
  if (!supabaseClient) {
    const url = process.env.NEXT_PUBLIC_SUPABASE_URL
    const key = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY

    console.log("[v0] Inicializando Supabase com URL:", url)
    if (!url || !key) {
      console.error("[v0] Variveis de ambiente do Supabase no configuradas")
      throw new Error("Variveis de ambiente NEXT_PUBLIC_SUPABASE_URL e NEXT_PUBLIC_SUPABASE_ANON_KEY so obrigatrias")
    }

    supabaseClient = createBrowserClient(url, key)
  }
  return supabaseClient
}

export const supabase = getSupabaseClient()
